<template>
  <div id="hum-toolkit">
    <!-- header for toolkit view -->
    <HumHeader>
      <!-- header bg -->
      <template v-slot:header-bg>
        <div class="toolkit-banner">
          <img class="toolkit-banner-bg" src="@/assets/toolkit/toolkit-header-bg.png" >
        </div>
      </template>

      <!-- header body -->
      <template v-slot:header-body>
        <div class="toolkit-header-body-text">
          <p>Speculative Crisis Kit</p>
        </div>
      </template>
    </HumHeader>

    <!-- content for toolkit view -->
    <div class="toolkit-content">
      <div class="content-main">
        <div class="c-content-header">
          <p>The Speculative Crisis Kit aims to help you and your team explore plausible longer-term futures, their potential impact on humanity and ways to mitigate such impacts.</p>
        </div>
        <div class="c-content-media">
          <img src="@/assets/toolkit/toolkit-content1.jpg" class="content-img">
          <img src="@/assets/toolkit/toolkit-content2.jpg" class="content-img">
        </div>
        <div class="c-content-qa">
          <div class="qa-wrapper">
            <p class="ques">What is this Toolkit?</p>
            <p class="ans">The Speculative Crisis Kit is a facilitators guide to conducting a workshop for anyone keen on exploring the future of crises and crisis response.</p>
          </div>
          <div class="qa-wrapper">
            <p class="ques">Why should one use it?</p>
            <p class="ans">The toolkit encourages one to think about the future through a more creative process involving making, storytelling, role playing and debating/ discpourse. In the process of thinking longterm it also prepares you for complex problems. The toolkit provides different frameworks to unlock existing knowledge that might be lying dormant.</p>
          </div>
          <div class="qa-wrapper">
            <p class="ques">What do I get?</p>
            <p class="ans">It gives you and your team a method to understand current trends and the way towards a desired future. Whether you make use of all tools or not, it will stimulate you to think differently and think long term. Depending on how deep you go into the toolkit, your takeaways will vary, but every part will help you learn or try something new.</p>
          </div>
          <div class="qa-wrapper">
            <p class="ques">Who is it meant for?</p>
            <p class="ans">Anybody interested in developing future scenarios for their organization, community or city by imagining crises and response. It can be expanded to fit any theme and practice.</p>
          </div>
          <div class="qa-wrapper">
            <p class="ques">When should I use it?</p>
            <p class="ans">You may use it to understand the future of your organization, or as a team building activity to understand each others preferences and beliefs or even as a casual afternoon activity to stimulate you brain to think long term.</p>
          </div>
        </div>
      </div>
    </div>

    <!-- toolkit footer -->
    <div class="toolkit-footer">
      <div class="footer-content">
        <div class="footer-info">
          <p>The toolkit is in its final stages of development, and the beta version will be available for public release soon.</p>
          <p>Please leave your details here, so that we can contact you once it’s out!</p>
        </div>
        <div class="footer-form">
          <p class="form-title">Please enter your contact details below</p>
          <div class="c-user-form">
            <!--  -->
            <div class="form-control name">
              <span>Name</span>
              <input type="text" v-model.trim="name">
            </div>
            <div class="form-control org">
              <span>Organisation (if any)</span>
              <input type="text" v-model.trim="organisation">
            </div>
            <div class="form-control email">
              <span>Email address</span>
              <input type="email" v-model.trim="email">
            </div>
            <div class="c-submit-btn">
              <button class="submit-btn" @click="submitData">Submit Details</button>
            </div>
          </div>
        </div>
      </div>
      <!-- toast for submitting data -->
      <div class="c-response-toast" :class="[showToast ? 'show' : '', dataSubmitted ? 'success' : 'error']">
        <svg-icon :name="toastIcon" class="toast-icon"></svg-icon>
        <span v-if="dataSubmitted" class="toast-msg">Details submitted.</span>
        <span v-else class="toast-msg">Details not submitted.</span>
      </div>
    </div>
  </div>
</template>

<script>
import HumHeader from '@/components/HumHeader.vue'
const axios = require('axios');

export default {
  name: 'Toolkit',
  components: {
    HumHeader,
  },
  data() {
    return {
      name: '',
      organisation: '',
      email: '',
      dataSubmitted: false,
      toastIcon: 'dotCircle',
      showToast: false
    }
  },
  methods: {
    submitData() {
      var self = this;
      if(self.name == '' || self.organisation == '' || self.email == '') {
        return;
      }else {
        var data = {
          name: self.name,
          organisation: self.organisation,
          email: self.email,
        }
        var options = {
          method: 'post',
          headers: { 'content-type': 'application/x-www-form-urlencoded' },
          url: 'https://script.google.com/macros/s/AKfycbykbzdtCbe-V-VURkiSUZV23Lb0bmRRfD3ilLJnulLkdWsDkwD6/exec',
          data: JSON.stringify(data)
        }
        axios(options)
        .then(function (response) {
          var result = response.data.result;
          if(result == 'success') {
            self.dataSubmitted = true;
            self.toastIcon = 'checkCircle';
          } else if(result == 'error') {
            self.dataSubmitted = false;
            self.toastIcon = 'alertCircle';
          }
          self.showToast = true;
          setTimeout(function() {
            self.showToast = false;
          }, 2500);
        })
        .catch(function (error) {
          // error
          self.dataSubmitted = false;
          self.toastIcon = 'alertCircle';
          self.showToast = true;
          setTimeout(function() {
            self.showToast = false;
          }, 2500);
        });
      }
    }
  }
}
</script>

<style lang="scss" scoped>
// toast styles
.c-response-toast {
  display: flex;
  align-items: center;
  position: fixed;
  right: 0;
  transform: translateX(calc(100% + 2.4rem));
  bottom: 2.4rem;
  background: $black;
  color: $white;
  padding: 0.6rem 0.8rem;
  border: 1px solid $white;
  transition: transform 0.3s ease-in;
  &.show {
    transform: translateX(0);
  }
  &.success {
    .toast-icon {
      fill: green;
    }
  }
  &.error {
    .toast-icon {
      fill: red;
    }
  }
  .toast-icon {
    font-size: 1.6rem;
    flex: 0 0 auto;
  }
  .toast-msg {
    margin-left: 0.8rem;
    font-size: 1.2rem;
    flex: 0 0 auto;
  }
}

#hum-toolkit {
  display: flex;
  flex-direction: column;
}
.toolkit-banner {
  position: absolute;
  left: 100%;
  height: 100%;
  transform: translateX(-50%);
  @include mq(480px) {
    left: 50%;
  }
  .toolkit-banner-bg {
    height: 100%;
  }
}
.toolkit-header-body-text {
  font-size: 3.6em;
  font-family: 'Space-semibold', sans-serif;
  color: $white;
  p {
    @include pad(2.4rem);
    background: $black;
    border-radius: 16px;
    text-align: center;
  }
}

.toolkit-content {
  display: flex;
  flex-direction: column;
  background: $white;
  .content-main {
    @include mar-v(2.4rem);
    @include pad-h(5%);
    display: flex;
    flex-direction: column;
    position: relative;
    @include mq($desktop-breakpoint) {
      @include mar-v(3.6rem);
      @include pad-h(15%);
      @include pad-r(50%);
    }
  }
}

.toolkit-content {
  .c-content-header {
    @include mar-v(1.5rem);
    font-size: 1.6em;
    font-family: 'Inter-semibold', sans-serif;
    @include mq($desktop-breakpoint) {
      @include mar-v(2rem);
      font-size: 1.8em;
      @include pad-r(15%);
    }
  }
  .c-content-media {
    display: flex;
    flex-direction: column;
    @include pad-v(1.5rem);
    @include mq($desktop-breakpoint) {
      @include pad-v(2rem);
      @include pad-r(15%);
      position: absolute;
      right: 0;
      top: 0;
      width: 49%;
    }
    .content-img {
      width: 100%;
      @include mar-b(1.5rem);
    }
  }
  .c-content-qa {
    @include mar-b(3rem);
    @include mar-t(1.5rem);
    display: flex;
    flex-direction: column;
    font-size: 1.2em;
    @include mq($desktop-breakpoint) {
      @include pad-r(15%);
    }
    .qa-wrapper {
      @include mar-v(2rem);
      .ques {
        font-family: 'Inter-semibold', sans-serif;
        font-weight: bold;
        @include mar-b(0.6rem);
      }
    }
  }
}

.toolkit-footer {
  background: $black;
  color: $white;
  display: flex;
  flex-direction: column;
  .footer-content {
    @include mar-h(5%);
    @include mar-v(4.8rem);
    display: flex;
    flex-direction: column;
    @include mq($desktop-breakpoint) {
      @include mar-h(15%);
      @include mar-v(9.6rem);
      flex-direction: row;

    }
    .footer-info {
      font-size: 1.6em;
      font-family: 'Inter-semibold', sans-serif;
      @include mar-v(1.6rem);
      @include mq($desktop-breakpoint) {
        flex: 1 0 0;
      }
      p {
        @include mar-b(2.4rem);
        @include mq($desktop-breakpoint) {
          padding-right: 15%;
        }
      }
    }
    .footer-form {
      @include mar-v(1.6rem);
      @include mq($desktop-breakpoint) {
        flex: 1 0 0;
      }
      p.form-title {
        font-size: 1.2em;
        font-family: 'Inter-semibold', sans-serif;
        @include mar-b(2.4rem);
        @include mq($desktop-breakpoint) {
          font-size: 1.6em;
        }
      }
      .c-user-form {
        font-size: 1.2em;
        display: flex;
        flex-direction: column;
        overflow: hidden;
        @include mq($desktop-breakpoint) {
          flex-direction: row;
          flex-wrap: wrap;
        }
        .form-control {
          @include mar-b(1.6rem);
          flex: 1 0 auto;
          display: flex;
          flex-direction: column;
          @include mq($desktop-breakpoint) {
            &:not(.email) {
              width: calc(50% - 0.8rem);
            }
            &.name {
              margin-right: 1.6rem;
            }
          }
          span {
            @include mar-b(0.6rem);
          }
          input {
            @include pad(0.6rem);
            // min font size of 16px for ios mobile devices to prevent zoom in
            font-size: 16px;
          }
        }
        .c-submit-btn {
          margin-bottom: 1.6rem;
          margin-top: 1.2rem;
          flex: 0 0 100%;
          display: flex;
          flex-direction: column;
          .submit-btn {
            color: $black;
            font-size: 1em;
            padding: 0.6rem;
            font-family: 'Inter', sans-serif;
            @include pad(0.6rem);
          }
        }
      }
    }
  }
}

</style>